import {
	Button,
	DatePicker,
	InputItem,
	List,
	Modal,
	Picker,
	Stepper,
	Switch,
} from "antd-mobile";
import { Dispatch, SetStateAction, useEffect, useState } from "react";
import Store from "../../../store";
import { JobStatus, Type } from "../../../utils/constant";
import { getChineseNumber } from "../../../utils/util";
import "./style.css";
interface IForm {
	onCloseModal: () => void;
	objId: string;
	setObjId: Dispatch<SetStateAction<string>>;
}
const Form = (props: IForm) => {
	const [companyName, setcompanyName] = useState("");
	const [positionName, setpositionName] = useState("");
	const [type, setType] = useState<string[] | undefined>();
	const [totalRounds, settotalRounds] = useState<number>(4); // ÊÄªËΩÆÊï∞
	const [needWritten, setNeedWritten] = useState(true);
	const [needHR, setNeedHR] = useState(true);
	const [linking, setLinking] = useState("");
	const [writtenTime, setWrittenTime] = useState<Date | undefined>();
	const [interviewTime, setInterviewTime] = useState<
		Array<Date | undefined> | []
	>([]);
	const [hrTime, setHrTime] = useState<Date | undefined>();
	const [min, setMin] = useState(3);

	const { progressStore } = Store;
	const { onCloseModal, objId, setObjId } = props;

	useEffect(() => {
		if (objId === "") return;
		const item = progressStore.interviewList.find((i) => i.objectId === objId);
		if (item) {
			setcompanyName(item.companyName);
			setpositionName(item.positionName);
			setType([item.type]);
			settotalRounds(
				item.totalRounds +
					(item.needWrittenExam ? 1 : 0) +
					(item.needHRinterview ? 1 : 0)
			); // item‰∏≠ÁöÑtotalRounds‰∏çÁÆóÁ¨îËØïhrÈù¢ÔºåÈúÄË¶ÅÂä†ÂõûÊù•
			setNeedHR(item.needHRinterview);
			setNeedWritten(item.needWrittenExam);
			setLinking(item.linking || "");
			setWrittenTime(item.timeList.written.time);
			setHrTime(item.timeList.hr.time);
			setInterviewTime(item.timeList.interview.map((i) => i.time));
			setMin(
				item.totalRounds +
					(item.needWrittenExam ? 1 : 0) +
					(item.needHRinterview ? 1 : 0)
			);
		}
	}, [objId, progressStore.interviewList]);

	const updateInterviewTime = (date: Date, index: number) => {
		interviewTime[index] = date;
		console.log([...interviewTime]);
		setInterviewTime([...interviewTime]);
	};

	const typePicker = [
		{ value: Type.ADVANCE, label: Type.ADVANCE },
		{ value: Type.FORMAL, label: Type.FORMAL },
		{ value: Type.SPRING, label: Type.SPRING },
		{ value: Type.TEST, label: Type.TEST },
	];

	const handleSubmit = () => {
		if (props.objId === "") {
			// Êñ∞Â¢ûitem
			if (companyName === "" || positionName === "") {
				Modal.alert("Waring", "ËØ∑ÂÆåÂñÑÂøÖÂ°´‰ø°ÊÅØ", [
					{ text: "OK", onPress: () => {} },
				]);
				return;
			}
			progressStore.addInterviewListItem({
				objectId: "",
				jobStatus: JobStatus.ING,
				companyName,
				positionName,
				type: !!type ? type[0] : Type.FORMAL,
				totalRounds: totalRounds - (needWritten ? 1 : 0) - (needHR ? 1 : 0),
				needWrittenExam: needWritten,
				needHRinterview: needHR,
				current: 0,
				timeList: {
					written: { time: !writtenTime ? undefined : writtenTime },
					interview: interviewTime.map((v) => {
						return { time: v };
					}),
					hr: { time: !hrTime ? undefined : hrTime },
				},
			});
		} else {
			// ‰øÆÊîπitem
			progressStore.updateInterviewListItem({
				objectId: objId,
				companyName,
				positionName,
				type: !!type ? type[0] : Type.FORMAL,
				totalRounds: totalRounds - (needWritten ? 1 : 0) - (needHR ? 1 : 0),
				needWrittenExam: needWritten,
				needHRinterview: needHR,
				timeList: {
					written: { time: !writtenTime ? undefined : writtenTime },
					interview: interviewTime.map((v) => {
						return { time: v };
					}),
					hr: { time: !hrTime ? undefined : hrTime },
				},
			});
		}
		onCloseModal();
		setObjId("");
	};

	const renderInput = (number: number) => {
		const res = [];
		for (let index = 0; index < number; index++) {
			res.push(
				<DatePicker
					value={interviewTime[index]}
					onChange={(date) => updateInterviewTime(date, index)}
					key={index}
				>
					<List.Item arrow="horizontal">
						{getChineseNumber(index + 1)}Èù¢Êó∂Èó¥
					</List.Item>
				</DatePicker>
			);
		}
		return res;
	};

	return (
		<List className="form-wrapper">
			<InputItem
				value={companyName}
				onChange={(e) => {
					setcompanyName(e);
				}}
				type="text"
				clear={true}
				placeholder="ËØ∑ËæìÂÖ•ÂÖ¨Âè∏Âêç"
			>
				* ÂÖ¨Âè∏
			</InputItem>
			<InputItem
				value={positionName}
				onChange={(e) => {
					setpositionName(e);
				}}
				type="text"
				clear={true}
				placeholder="ËØ∑ËæìÂÖ•ËÅå‰ΩçÂêçÁß∞"
			>
				* ËÅå‰Ωç
			</InputItem>
			<Picker
				data={typePicker}
				value={type}
				onChange={(e) => setType(e as string[])}
				cols={1}
				className="forss"
			>
				<List.Item arrow="horizontal">* ÈÄâÊã©ÊäïÈÄíÁ±ªÂûã</List.Item>
			</Picker>
			<List.Item
				extra={
					<Switch
						checked={needWritten}
						onChange={(e) => {
							setNeedWritten(e);
							if (e) {
								settotalRounds(totalRounds + 1);
								setMin(min + 1);
							} else {
								settotalRounds(totalRounds - 1);
								setMin(min - 1);
							}
						}}
						platform="ios"
						disabled={objId !== ""}
					/>
				}
			>
				* ÊòØÂê¶ÈúÄË¶ÅÁ¨îËØï
			</List.Item>
			<List.Item
				extra={
					<Switch
						checked={needHR}
						onChange={(e) => {
							setNeedHR(e);
							if (e) {
								settotalRounds(totalRounds + 1);
								setMin(min + 1);
							} else {
								settotalRounds(totalRounds - 1);
								setMin(min - 1);
							}
						}}
						platform="ios"
						disabled={objId !== ""}
					/>
				}
			>
				* ÊòØÂê¶ÈúÄË¶ÅHRÈù¢
			</List.Item>
			<List.Item
				wrap
				extra={
					<Stepper
						showNumber
						max={8}
						min={min}
						value={totalRounds}
						onChange={(e) => {
							// Á¨îËØïhrÈù¢ÈÉΩÈúÄË¶ÅÁöÑÊó∂ÂÄôÔºåÊÄªËΩÆÊï∞‰∏çËÉΩÂ∞è‰∫é3
							if (needWritten && needHR && e < 3) return;
							// Á¨îËØïhrÈù¢ÈúÄË¶ÅÂÖ∂‰∏ÄÁöÑÊó∂ÂÄôÔºåÊÄªËΩÆÊï∞‰∏çËÉΩÂ∞è‰∫é2
							else if (e < 2 && (needHR || needHR)) return;
							settotalRounds(e);
						}}
					/>
				}
			>
				* Èù¢ËØïÊÄªËΩÆÊï∞
			</List.Item>
			<List.Item>
				<div className="reminder">*** ‰ª•‰∏ãÂÜÖÂÆπÈÄâÂ°´ ***</div>{" "}
			</List.Item>
			{needWritten ? (
				<DatePicker
					value={writtenTime}
					onChange={(date) => {
						setWrittenTime(date);
						console.log(date);
					}}
				>
					<List.Item arrow="horizontal"> Á¨îËØïÊó∂Èó¥</List.Item>
				</DatePicker>
			) : null}
			{renderInput(totalRounds - (needHR ? 1 : 0) - (needWritten ? 1 : 0))}
			{needHR ? (
				<DatePicker value={hrTime} onChange={(date) => setHrTime(date)}>
					<List.Item arrow="horizontal"> hrÈù¢Êó∂Èó¥</List.Item>
				</DatePicker>
			) : null}
			<InputItem
				value={linking}
				onChange={(e) => {
					setLinking(e);
				}}
				type="text"
				clear={true}
				placeholder="ËØ∑ËæìÂÖ•ÊäïÈÄíÈìæÊé•"
			>
				ÊäïÈÄíÈìæÊé•
			</InputItem>
			<Button onClick={handleSubmit}>Êèê‰∫§</Button>
		</List>
	);
};
export default Form;
